<template>
  <div class="box">
    <el-form
      ref="orderForm"
      :model="orderFormData"
      :validate-on-rule-change="false"
      label-width="90px"
      @submit.native.prevent
    >
      <el-form-item label="组件名:">
        <!-- <el-input
          v-model="orderFormData.name"
          placeholder="请输入标题"
          @input="debounceHandler"
          @keyup.native.enter="debounceHandler"
        /> -->
        <span>{{ orderFormData.name }}</span>
      </el-form-item>
      <el-form-item label="入参:">
        <el-input
          v-model="orderFormData.description"
          :autosize="{ minRows: 3, maxRows: 4 }"
          type="textarea"
          placeholder="请输入入参"
          @input="debounceHandler"
          @keyup.native.enter="debounceHandler"
        />
      </el-form-item>
      <el-form-item label="出参:">
        <el-input
          v-model="orderFormData.description"
          :autosize="{ minRows: 3, maxRows: 4 }"
          type="textarea"
          placeholder="请输入出参"
          @input="debounceHandler"
          @keyup.native.enter="debounceHandler"
        />
      </el-form-item>
      <el-form-item label="">
        <el-button type="text" @click="handleViewLog">
          查看日志链接
        </el-button>
      </el-form-item>
    </el-form>
    <viewLogDialog ref="viewLogDialog" />
  </div>
</template>

<script>
import { debounce, cloneDeep } from 'lodash';
import viewLogDialog from '../viewLogDialog.vue';

export default {
  name: 'EditOrder',
  components: {
    viewLogDialog,
  },
  props: {
    formData: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      mapVisible: false,
      mode: '',
      orderFormData: {
        name: '',
        description: '',
      },
    };
  },
  computed: {
    isVisibile: {
      get() {
        return this.dialogVisibleAddOrder;
      },
      set() {},
    },
  },
  watch: {
    formData: {
      handler(val) {
        // let sourForm=this.$options.data.orderFormData;
        console.log('val formData', val);
        Object.assign(this.orderFormData, val);
      },
      deep: true,
      immediate: true,
    },
  },
  mounted() {

  },
  methods: {
    handleViewLog() {
      this.$refs.viewLogDialog.show(this.orderFormData);
    },
    // 当关闭添加命令弹窗
    onAddOrderDialgClose() {
      const data = this.$options.data();
      this.orderFormData = data.orderFormData;
      this.$emit('cancelorder');
    },
    // 添加行为
    handleAddOrder() {
      // eslint-disable-next-line consistent-return
      this.$refs.orderForm.validate(async (valid) => {
        if (valid) {
          this.$emit('confirmorder', cloneDeep(this.orderFormData));
        } else {
          return false;
        }
      });
    },

    handlerChange() {
      this.$emit('updateCellData', cloneDeep(this.orderFormData));
    },
    debounceHandler: debounce(function () {
      this.handlerChange();
    }, 1000),
  },
};
</script>
<style lang="scss" scoped>
::v-deep .el-form-item__label {
